.checkout-page-container
{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    border: 2px solid lightgray;
    /* background-color: rgb(247, 243, 243); */
    padding: 50px;
    border-radius: 10px;
    column-gap: 10px;    
    text-align: left;
    margin: 0 auto;
}

.title {
    text-align: left;

}

label {
    font-size: 0.85rem;
    /* display: flex; */
    text-align: left;
    /* margin-top: 10px; */
    /* margin-bottom: 5px; */
    margin-top: 20px;
   
  }

.radio {
    /* display: flex; */
    margin: 0;
    margin-bottom: 25px;
    padding-left: 20p
    
}

.my-radio-group {
    margin-bottom: 0;
}

.text--label {
    padding-left: 20px; 
}
.paypal {
    margin-top: 3px;
}

.credit {
    margin-top: 3px;
}

.radio label {
    margin-left: 25px;
    margin-top: 0px;
}

form {
    display: grid;
    width: 400px;
    
}



.btn-submit {
    margin-top: 20px;
    border: 1px solid black;
    background-color: black;
    color: white;
    padding: 5px;
    min-width: 400px;
}

.btn-submit:hover{
    background-color: darkgrey;
    color: black;
    border: 1px solid black;
    
}

.info-box {
    background-color: rgb(213, 221, 230);
    padding: 30px;
    border-radius: 10px;
}

.img-paypal {
    background-color: darkgrey;
}

input[type="email"],
input[type="text"],
input[type="number"] {
  padding: 0.1rem 0.2rem;
  font-size: 1rem;
  background-color: white;
  border: 1px solid darkgrey;
  /* margin-bottom: 10px; */
}

.credit-info {
    display: flex;
    text-align: left;
    margin-top: 20px;
}

.credit-info input {
    width: 160px;
}

.error {
    font-size: 0.75rem;
    text-align: left;
    color: red;    
    margin: 0;
}

.credit-info-error {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    /* display: flex;  */
    /* text-align: left; */
}

.ccv{
    margin-left: 12px;
}

.ssl-message {
    font-size: 0.75rem;
    text-align: left;
}

.field {
    min-width: 400px;
    
}

.total {
    text-align: left;
}


@media screen and (max-width: 996px) {

    .formik {
        margin: 0 auto;
        justify-content: center;
        /* background-color: red; */
    }

    .checkout-page-container{
        grid-template-columns: 1fr;
        justify-content: center;
        
    }

    .info-box {
        margin-bottom: 20px;
        
    }

    /* .App {
        background-color: red;
    } */

    
}