p {
    color: rgb(138, 135, 135);
}


.checkoutContent {
    
    border: 2px solid rgb(218, 214, 214);
    padding: 30px;
    text-align: left;
    min-width: 250px;
    transition: 0.3s;
}

.checkoutContent:hover {
    background-color: rgb(233, 232, 232);
}


p {
    color: rgb(138, 135, 135);    
}

.buttonsDiv {
    border-top: 2px solid rgb(138, 135, 135);
    padding-top: 30px;

}

.btn-checkout {
    color: white;
    background-color: black;
    border: none;
    font-size: 12px;
    padding: 15px;
    text-transform: uppercase;
    transition: 0.2s;
}

.btn-checkout a {
    color: white;
    
}

.btn-checkout:hover {
    background-color: rgb(77, 74, 74);
    
}

.btn-clear {
    background-color: white;
    border: none;
    text-transform: uppercase;
    font-size: 10px;
    padding: 15px;
    color: rgb(102, 100, 100);
    transition: 0.2s;
    margin-left: 5px;
}

.btn-clear:hover {
    
    border: 0.2px solid black;
    
}


.container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    margin-top: 60px;
    
    
}

.cartContent {
    grid-column: 1 / 10;
}

.checkout {
    grid-column: 10 / 12;
    
}

@media screen and (max-width: 1034px) {

    /* .container {
        display: grid;
