    .cartContent {
        grid-column: 1 / 12;
        margin: 0 auto;
        
    }   

    .checkout {
        grid-column: 4 / 10;
        justify-content: center;
        
    }
}
.checkout-page-container
{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
